
class Component {
    constructor(x, y, width, height, angle) {
        this.x = x
        this.y = y
        this.width = width
        this.height = height
        this.angle = angle
    }

    draw(context) {
        context.save()
        context.beginPath()
        context.translate(this.x, this.y)
        context.rotate(this.angle)
        this.drawComponent(context)
        context.closePath()
        context.restore()    
    }

    boundingbox() {

        const cos = Math.cos(-this.angle)
        const sin = Math.sin(-this.angle)
        const sinw = sin * this.width
        const cosw = cos * this.width
        const sinh = sin * this.height
        const cosh = cos * this.height
        const p1 = {x: this.x, y: this.y}
        const p2 = { x: p1.x + cosw, y:p1.y - sinw }
        const p3 = { x: p2.x + sinh, y:p2.y + cosh }
        const p4 = { x: p3.x - cosw, y:p3.y + sinw }
        const xmin = Math.min(p1.x, p2.x, p3.x, p4.x)
        const xmax = Math.max(p1.x, p2.x, p3.x, p4.x)
        const ymin = Math.min(p1.y, p2.y, p3.y, p4.y)
        const ymax = Math.max(p1.y, p2.y, p3.y, p4.y)

        return {
            p1 : p1,
            p2 : p2,
            p3 : p3,
            p4 : p4,
            xmin : xmin,
            xmax : xmax,
            ymin : ymin,
            ymax : ymax
        }
    }

    testIntersection(a, b, boundingbox) {
        function onSegment(p,q,r) {
            return (q.x <= Math.max(p.x, r.x) && q.x >= Math.min(p.x, r.x) && q.y <= Math.max(p.y, r.y) && q.y >= Math.min(p.y, r.y)) 
        }
        function orientation(p,q,r) {
            const val = (q.y - p.y) * (r.x - q.x) - (q.x - p.x) * (r.y - q.y); 
            if (val == 0) {
                return 0; // colinear 
            } else if(val > 0) {
                return 1
            } else {
                return 2
            }
        }
        function intersect(p1,q1,p2,q2) {
            const o1 = orientation(p1, q1, p2); 
            const o2 = orientation(p1, q1, q2); 
            const o3 = orientation(p2, q2, p1); 
            const o4 = orientation(p2, q2, q1); 
        
            // General case 
            if (o1 != o2 && o3 != o4) {
                return true;
            }
        
            // Special Cases 
            // p1, q1 and p2 are colinear and p2 lies on segment p1q1 
            if (o1 == 0 && onSegment(p1, p2, q1)) {
                return true 
            }
        
            // p1, q1 and q2 are colinear and q2 lies on segment p1q1 
            if (o2 == 0 && onSegment(p1, q2, q1)) {
                return true
            }
        
            // p2, q2 and p1 are colinear and p1 lies on segment p2q2 
            if (o3 == 0 && onSegment(p2, p1, q2)) {
                return true; 
            }
        
            // p2, q2 and q1 are colinear and q1 lies on segment p2q2 
            if (o4 == 0 && onSegment(p2, q1, q2)) return true; 
        
            return false; // Doesn't fall in any of the above cases 
        }

        if(intersect(a,b, boundingbox.p1,boundingbox.p2)) {
            return true
        }
        if(intersect(a,b, boundingbox.p2,boundingbox.p3)) {
            return true
        }
        if(intersect(a,b, boundingbox.p3,boundingbox.p4)) {
            return true
        }
        if(intersect(a,b, boundingbox.p4,boundingbox.p1)) {
            return true
        }
        return false
    }


    collisionBoundingBox(a, b, boundingbox) {
        
        const xmin = boundingbox.xmin
        const xmax = boundingbox.xmax
        const ymin = boundingbox.ymin
        const ymax = boundingbox.ymax

        if(a.x < xmin && b.x < xmin) {
            return false
        }        
        if(a.x > xmax && b.x > xmax) {
            return false
        }        
        if(a.y < ymin && b.y < ymin) {
            return false
        }        
        if(a.y > ymax && b.y > ymax) {
            return false
        }
        function testp(a,b,p) {
            return (p - a >= 0 && b - p >= 0) || (p - b >= 0 && a - p >= 0)
        }
        if(a.x == b.x) {
            if(a.x >= xmin && a.x <= xmax) {
                if(testp(a.y, b.y, ymin)) {
                    return true
                }
                if(testp(a.y,b.y, ymax)) {
                    return true
                }
                return false;
            } else {
                return false;
            }
        }
        if(a.y == b.y) {
            if(a.y >= ymin && a.y <= ymax) {
                if(testp(a.x, b.x, xmin)) {
                    return true
                }
                if(testp(a.x, b.x, xmax)) {
                    return true
                }
                return false;
            } else {
                return false;
            }

        }
        
        const rc = (b.x - a.x)/(b.y - a.y) 
        const B = a.y - rc * a.x 
        const s1 = rc * xmin + B
        const s2 = rc * xmax + B
        const s3 = (ymin - B) / rc
        const s4 = (ymax - B) / rc

        if(testp(a.y, b.y, s1)) {
            return true
        }
        if(testp(a.y, b.y, s2)) {
            return true
        }
        if(testp(a.x, b.x, s3)) {
            return true
        }
        if(testp(a.x, b.x, s4)) {
            return true
        }
        return false
    }

    collision(a,b, boundingbox) {
        if(this.collisionBoundingBox(a,b, boundingbox)) {
            return this.testIntersection(a,b,boundingbox)
        } else {
            return false
        }
    }

}

class LeftLander extends Component {
    constructor(x, y, s) {
        super(x, y, 0.5 *s, s, 0)
    }
    drawComponent(context) {
        const foot = this.width
        const leg = this.height
        context.moveTo(0,0)
        context.lineTo(-foot,0)
        context.moveTo(-foot/2, 0)
        context.lineTo(-foot/2, - 0.5 * leg)
        context.lineTo(-foot, - 0.75 * leg)
        context.moveTo(-foot/2, - 0.5 * leg)
        context.lineTo(-foot, -leg)
        context.lineTo(-foot, -0.75 * leg)
        context.strokeStyle = "white"
        context.stroke()
    }
}

class RightLander extends Component {
    constructor(x, y, s) {
        super(x, y, 0.5 * s, s, 0)
    }
    drawComponent(context) {
        const foot = this.width
        const leg = this.height
        context.moveTo(0,0)
        context.lineTo(foot,0)
        context.moveTo(foot/2,0)
        context.lineTo(foot/2,- 0.5 * leg)
        context.lineTo(foot, - 0.75 * leg)
        context.moveTo(foot/2, - 0.5 * leg)
        context.lineTo(foot, -leg)
        context.lineTo(foot, -0.75 * leg)
        context.strokeStyle = "white"
        context.stroke()
    }
}


class Engine extends Component {
    constructor(x, y, s) {
        super(x, y, s, s, 0)
        this.on = false
        this.fuel = 1000
    }

    drawComponent(context) {
        context.rect(0, 0, this.width, this.height)
        context.strokeStyle = "white"
        context.stroke()

        if(this.on && this.fuel > 0) 
        {
            context.beginPath();
            context.moveTo(0, this.height);
            context.lineTo(this.width, this.height);
            context.lineTo(this.width * 0.5, this.height + Math.random() * 5);
            context.lineTo(0, this.height);

            context.closePath();
            context.fillStyle = "orange";
            context.fill();
        }
    }

}


class Cockpit extends Component {
    constructor(x, y, r) {
        super(x, y, 3 * r, 2 * r, 0)
        this.r = r
    }

    drawComponent(context) {
        context.arc( 0, 0, this.r, 0.5 * Math.PI, 1.5 * Math.PI, false)
        context.lineTo( 2 * this.r, -this.r)
        context.moveTo(2 * this.r, this.r)
        context.arc(2 * this.r, 0, this.r, 0.5 * Math.PI, 1.5 * Math.PI, true)
        context.moveTo(2 * this.r, this.r)
        context.lineTo( 0, this.r)
        context.strokeStyle = "white"
        context.stroke()
    }
}


export default class SpaceShip extends Component {

    constructor(canvas, size) {
        super( canvas.width * 0.50, canvas.height * 0.10, 2 * size, 2.5 * size, 0)
        this.size = size
        this.state = 'waiting'
        this.startPosition = {x: this.x, y: this.y}
        this.reset()
    }
    
    reset() {
        const s = this.size
        this.cockpit = new Cockpit( 0.5 * s, 0.5 * s, 0.5 * s, 0)
        this.engine = new Engine(0.5 * s, s,  s)
        this.leftLander = new LeftLander( 2 * s, 2.5 * s, s)
        this.rightLander = new RightLander(0, 2.5 * s, s)
        this.x = this.startPosition.x 
        this.y = this.startPosition.y 
        this.angle = 0
        this.engine.on = false
        this.rotatingLeft = false
        this.rotatingRight = false
        this.velocity = { x:0 , y:0 }
        this.thrust = 0.1
        this.crashed = false
        this.state = 'waiting'
    }

    getVelocity(){
        return Math.sqrt(Math.pow(this.velocity.x,2) + Math.pow(this.velocity.y,2))
    }

    draw(context) {
        super.draw(context)
    }

    drawComponent(context) {
       this.leftLander.draw(context)
       this.rightLander.draw(context)
       this.engine.draw(context)
       this.cockpit.draw(context)
    }

    collisionWith(item) {
        if(item == 'landingpad') {
            if(this.velocity.x < 1 && this.velocity.y < 2 && this.angle < 0.12 && this.angle > -0.12) {
                // Game.started = false
                this.state = 'landed'
            } else {
                this.state = 'crashed'
                this.leftLander.angle = -1
                this.rightLander.angle = 1
                this.cockpit.angle = 0.4
                this.engine.angle = -0.3
                this.engine.y=this.height
                this.cockpit.y=this.height        
            }
        } else {
            this.state = 'crashed'
            this.leftLander.angle = -1
            this.rightLander.angle = 1
            this.cockpit.angle = 0.4
            this.engine.angle = -0.3
            this.engine.y=this.height
            this.cockpit.y=this.height        
        }
    }

    update() {
        const gravity = 0.03
        if(this.rotatingRight) {
            this.angle += Math.PI / 180;
        }
        else if(this.rotatingLeft) {
            this.angle -= Math.PI / 180;
        }

        if(this.state == 'active') {
            this.x += this.velocity.x
            this.y += this.velocity.y
            if(this.engine.on && this.engine.fuel > 0) {
                this.engine.fuel -= 1
                this.velocity.x += this.thrust * Math.sin(this.angle);
                this.velocity.y -= this.thrust * Math.cos(this.angle);
            }
            this.velocity.y += gravity
        }
    }
}
