class Mountains {
    
    constructor() {
        this.points = []    
    }

    initialize(canvas) {
        var max = canvas.width / 20
        const maxy = canvas.height/10
        var currentWidth = 0
        this.points[0] = { x: 0, y: canvas.height - Math.random() *  0.6 * canvas.height + (Math.random() *  0.05 + 0.03) * canvas.height }
        for(var i = 1 ; i < 30; i ++) {
            if(i == 15) {
                this.points[i] = { x: this.points[i-1].x + 60, y:  this.points[i-1].y }
                currentWidth += 60
            } else {
                var y = this.points[i-1].y + ( 0.5 * maxy - Math.random() * maxy ) + 0.03 * canvas.height
                if(y < (canvas.height * 0.6)) {
                    y = canvas.height * 0.6
                }
                if(y > (canvas.height * 0.9)) {
                    y = canvas.height * 0.9
                }
                var d =  Math.random() * ( max < canvas.width - currentWidth ? max : canvas.width - currentWidth)
                this.points[i] = {x:currentWidth + d, y:y}
                currentWidth += d
            }
        }
        this.points[30] = {x: canvas.width, y: canvas.height - Math.random() *  0.6 * canvas.height }
        this.points[31] = {x: canvas.width, y:canvas.height }
        this.points[32] = {x: 0, y:canvas.height }
        this.points[33] = {x: 0, y: this.points[0].y }
    }

    draw(context) {
        context.save()
        context.beginPath()
        context.moveTo(this.points[0].x, this.points[0].y)
        for(var i = 1 ; i < 34; i ++) {
            context.lineTo(this.points[i].x, this.points[i].y)
        }

        context.fillStyle = "black"
        context.strokeStyle = "white"
        context.stroke()
        context.fill()
        context.closePath()
        context.restore()

        //draw landing
        context.save()
        context.beginPath()
        context.lineWidth = 3
        context.moveTo(this.points[14].x, this.points[14].y)
        context.lineTo(this.points[15].x, this.points[15].y)
        context.strokeStyle = "green"
        context.stroke()
        context.closePath()
        context.restore()
    }

    collision(item) {
        for(var i = 1 ; i < this.points.length; i++) {
            if(item.collision(this.points[i-1], this.points[i], item.boundingbox())) {
                if(i == 15) { //landingpad 
                    item.collisionWith("landingpad")
                } else {
                    item.collisionWith("mountain")
                }
            }
        }
    }
}


class Stars {

    constructor() {
        this.stars = []
    }
    initialize(canvas) {
        for (var i = 0; i < 500; i++) {
            this.stars[i] = {
                x: Math.random() * canvas.width,
                y: Math.random() * canvas.height,
                radius: Math.sqrt(Math.random() * 2),
                alpha: 1.0,
                decreasing: true,
                dRatio: Math.random()*0.05
            }
        }
    }
    draw(context) {
        context.save();
        context.fillStyle = "#111"
        for (var i = 0; i < this.stars.length; i++) {
            var star = this.stars[i];
            context.beginPath();
            context.arc(star.x, star.y, star.radius, 0, 2*Math.PI);
            context.closePath();
            context.fillStyle = "rgba(255, 255, 255, " + star.alpha + ")";
            if (star.decreasing == true) {
                star.alpha -= star.dRatio;
                if (star.alpha < 0.1) { 
                    star.decreasing = false;
                }
            } else {
                star.alpha += star.dRatio;
                if (star.alpha > 0.95) { 
                    star.decreasing = true;
                }
            }
            context.fill();
            context.restore();
        }
    } 
    collision(item) {
        return item;
    }
}

export default class Scenery {

    constructor(canvas) {
        this.items = [ new Stars(), new Mountains() ]
        this.items.forEach(element => {
            element.initialize(canvas)
        });
    }

    draw(context) {
        this.items.forEach(element => {
            element.draw(context)
        });
    }

    collision(item) {
        this.items.forEach(element => {
            element.collision(item)
        });
    }
}