<template>
  <div class="lunar">
      <canvas ref="lunar" width="800" height="500">
      </canvas>
  </div>
</template>
<style scoped>
.lunar canvas { border: 1px solid black; background-color: black;}
</style>

<script>
import Game from './Game.js'

export default {
  name: "LunarLander",
  methods: {
    draw(){
        var canvas = this.$refs.lunar;
        const game = new Game(canvas)
        game.registerKeyListeners()
        var context = canvas.getContext("2d");

        function drawFrame() {
            context.clearRect(0, 0, canvas.width, canvas.height);
            game.update()
            game.draw(context)
            requestAnimationFrame(drawFrame);
        }
        drawFrame()
    }
  },
  mounted: function() {
      this.draw()
  }
};
</script>
